body {
    margin: 0;
    padding: 0;
    font-family: 'Segoe UI', sans-serif;
    background-color: #0d1117;
    color: #f5f5f5;
  }
  
  .App {
    text-align: center;
    padding: 2rem;
  }
  
  .grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
    gap: 1.5rem;
    margin-top: 1rem;
  }
  
  .bot-card {
    background-color: #161b22;
    border: 1px solid #30363d;
    border-radius: 12px;
    padding: 1rem;
    cursor: pointer;
    transition: transform 0.2s ease;
  }
  
  .bot-card:hover {
    transform: scale(1.05);
    border-color: #58a6ff;
  }
  
  .bot-card img {
    width: 100%;
    border-radius: 8px;
  }
  
  .bot-card button {
    margin-top: 10px;
    background-color: crimson;
    color: white;
    border: none;
    padding: 6px 10px;
    border-radius: 6px;
    cursor: pointer;
  }
  
  .bot-specs {
    background-color: #21262d;
    padding: 2rem;
    border-radius: 12px;
    max-width: 500px;
  }